import './App.css';
import React, { Suspense, useEffect } from 'react';
import Header from './layouts/header/Header';
import { Routes, Route } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { fetchProducts } from './redux/productsSlice/productsSlice';
import Spinner from './components/spinner/Spinner';

const HomePage = React.lazy(() => import('./pages/home/HomePage'));
const Products = React.lazy(() => import('./pages/products/Products'));
const NotFound = React.lazy(() => import('./pages/notFound/NotFound'));
const Detail = React.lazy(() => import('./pages/detail/DetailPage'));
const Footer = React.lazy(() => import('./layouts/footer/Footer'));
const CheckoutComponent = React.lazy(() => import('./components/checkout/CheckoutComponent'));

function App() {

  const dispatch = useDispatch()
  useEffect(() => {
    getLocalStorageThemeColor();
    dispatch(fetchProducts());
  }, [dispatch]);

  function getLocalStorageThemeColor() {
    const storedTheme = localStorage.getItem('theme');
    if (storedTheme) {
      document.body.classList.add(storedTheme);
    }
  }

  return (
    <>
      <Header />
      <main>
        <Suspense fallback={<Spinner />}>
          <Routes>
            <Route path='/' element={<HomePage />} />
            <Route path='/products' element={<Products />} />
            <Route path='/product/:id' element={<Detail />} />
            <Route path='/products/:category' element={<Products />} />
            <Route path='/checkout' element={<CheckoutComponent />} />
            <Route path='*' element={<NotFound />} />
          </Routes>
        </Suspense>
      </main>
      <Footer />
    </>
  );
}

export default App;
